<template>
  <div>
    <div class="search-con search-con-top">
      <Input class="search-input" v-model="initPara.searchVal" search :maxlength='50' @on-search="handleSearch"
        placeholder="" />
      <h4 v-if="id" class="search-col">{{id}}</h4>
      <Button 
        type="primary" 
        class="search-add-btn" 
        @click="handleImport"
      >
        
      </Button>
      <Button v-if="!id" type="error" class="search-add-btn" @click="handleDel"
        :disabled="selectList.length !== 0 ? false : true"></Button>
    </div>

    <Table ref="selection" size="small" :columns="tableColumns" :data="tableData" :loading="i.tableLoading"
      @on-sort-change="handleSort" @on-selection-change="handleSelect"></Table>

    <!--  -->
    <div style="margin: 10px; overflow: hidden">
      <div style="float: left;">
        <Page :total="i.pageTotal" :current="initPara.page_number" :page-size="initPara.page_size" show-total show-sizer
          :page-size-opts="[15, 50, 100, 1000]" @on-change="handlerChangePage" @on-page-size-change="handlePageSize" />
      </div>
    </div>

    <!-- <div v-if="natData">
      <NatDetail :modal-data="modalData" :nat-data="natData" />
    </div> -->

    <!--  -->
    <Modal
      v-model="importModal.show"
      :title="importModal.title"
      :mask-closable="false"
    >
      <div class="import-container">
        <Upload
          ref="upload"
          :show-upload-list="false"
          :before-upload="handleBeforeUpload"
          action="#"
          :auto-upload="false"
        >
        <!-- <div class="button-group"> -->
          <Button icon="ios-cloud-upload-outline" type="primary"></Button>
          <Button type="text" @click="downloadTemplate" class="download-btn">
          <Icon type="ios-download-outline" /></Button>
        <!-- </div> -->
        </Upload>

        <!--  -->
        <div v-if="previewData.length" class="preview-wrapper mt-16">
          <div class="preview-header">
            <span> {{ previewData.length }} </span>
          </div>
          <Table 
            :columns="previewColumns" 
            :data="previewData"
            :height="300"
            size="small"
          ></Table>
        </div>
      </div>
      <div slot="footer">
        <Button @click="importModal.show = false"></Button>
        <Button 
          type="primary" 
          :loading="importing"
          @click="handleImportSubmit"
          :disabled="previewData.length === 0"
        >
          
        </Button>
      </div>
    </Modal>

    <!--  -->
    <Modal
      v-model="resultModal.show"
      title=""
      :mask-closable="false"
    >
      <div class="result-content">
        <div class="result-item">
          <span></span>
          <span class="success-text">{{ importResult.success_count }}</span>
        </div>
        <div class="result-item">
          <span></span>
          <span class="error-text">{{ importResult.failed_count }}</span>
        </div>
        <div v-if="importResult.errors.length" class="error-list">
          <div class="error-title"></div>
          <div class="error-scroll">
            <Alert 
              v-for="(error, index) in importResult.errors" 
              :key="index"
              type="error"
              show-icon
            >
              {{ error }}
            </Alert>
          </div>
        </div>
      </div>
      <div slot="footer">
        <Button @click="handleResultClose"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { importSwitch, getSwitch, optSwitch } from '@/api/cloud/switch.js'
import * as XLSX from 'xlsx'
export default {
  name: 'Switch',
    props: {
    id: {
      type: String,
      default: null
    },
  },
  data () {
    return {
      natData: {},
      tableColumns: [
        {
          type: "selection",
          key: "selection",
          width: 60,
          align: "center",
        },
        {
          title: '',
          key: 'name',
          align: "center",
          tooltip: true,
          sortable: true,
          minWidth: 150
        },
        {
          title: 'IP',
          key: 'manage_ip',
          ellipsis: true,
          tooltip: true,
          minWidth: 150,
        },
        {
          title: '',
          key: 'sn',
          minWidth: 150
        },
        {
          title: 'MAC',
          key: 'mac_address',
          minWidth: 100
        },
        {
          title: '',
          key: 'vendor',
          align: "center",
          minWidth: 150
        },
        {
          title: '',
          key: 'model',
          align: "center",
          minWidth: 150,
        },
        {
          title: '',
          key: 'idc',
          minWidth: 90
        },
        {
          title: '',
          key: 'rack',
          minWidth: 90
        },
        {
          title: 'U',
          key: 'position',
          minWidth: 90
        },
        {
          title: '',
          key: 'role',
          minWidth: 90
        },
        {
          title: '',
          key:'status',
          minWidth: 90
        },
        {
          title: '',
          key: 'description',
          minWidth: 100
        },
        {
          title: '',
          key: 'create_time',
          minWidth: 100
        }

      ],
      tableData: [],
      i:{
        pageTotal: 0, //
        tableLoading: true
      },
      initPara:{
        page_number: 1,
        page_size: 15,
        searchVal: null,
        // sg_ids: [],
        order_by: "",      //
        order: "ascend", // ascend descend
      },
      selectList: [],
      // 
      importModal: {
        show: false,
        title: ''
      },
      resultModal: {
        show: false
      },
      importing: false,
      previewData: [],
      importResult: {
        success_count: 0,
        failed_count: 0,
        errors: []
      },
      previewColumns: [
        {
          title: '',
          key: 'name',
          width: 150
        },
        {
          title: 'IP',
          key: 'manage_ip',
          width: 100
        },
        {
          title: '',
          key: 'sn',
          width: 90
        },
        {
          title: 'MAC',
          key: 'mac_address',
          width: 90
        },
        {
          title: '',
          key: 'vendor',
          width: 150
        },
        {
          title: '',
          key:'model',
          width: 150
        },
        {
          title: '',
          key: 'idc',
          width: 150
        },
        {
          title: '',
          key: 'rack',
          width: 150
        },
        {
          title: 'U',
          key: 'position',
          width: 100
        },
        {
          title: '',
          key: 'role',
          width: 150
        },
        {
          title: '',
          key:'status',
          width: 100
        },
        {
          title: '',
          key: 'description',
          width: 150
        }
      ]
    }
  },
  methods: {
    // 
    async handleGetData() {
      const res = await getSwitch(this.initPara);
      if (res.code == 0) {
        this.tableData = res.data;
        this.i.pageTotal = res.count
      } else {
        this.$Message.error(res.msg)
      }
      this.i.tableLoading = false
    },
    // 
    handleDel() {
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          optSwitch({ id_list: this.selectList }, 'delete').then(res => {
            if (res.code === 0) {
              this.$Message.success(res.msg)
              this.handleGetData()
            } else {
             this.$Notice.error({ title: `${res.msg}` })
            }
          })
        },
        onCancel: () => {
          this.$Message.info('~')
        },
      })
    },
    // 
    handleDetail(paramsRow) {
      this.natData = paramsRow
      this.modalData.show = true
    },
    handleClose() {
      this.modalAdd.show = false
      this.modalData.show = false
      this.handleGetData()
    },
    handleSelect (val) {
      let selectIdList = []
      val.forEach(item => {
        selectIdList.push(item.id)
      })
      this.selectList = selectIdList
    },
    handleSearch(){
      this.initPara.page_number = 1
      this.handleGetData()
    },
    handleSort (column, key, order) {
      this.initPara.order = column.order === "asc" ? 'ascend' : 'descend'
      this.initPara.order_by = column.key
      this.handleGetData()
    },
    // 
    handlerChangePage (value) {
      this.initPara.page_number = value
      this.handleGetData()
    },
    handlePageSize (value) {
      this.initPara.page_number = 1
      this.initPara.page_size = value
      this.handleGetData()
    },
    // 
    handleImport() {
      this.importModal.show = true
      this.previewData = []
    },

    // 
    handleBeforeUpload(file) {
      // 
      const fileTypes = [
        'application/vnd.ms-excel',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'text/csv'
      ]
      const isValidType = fileTypes.includes(file.type)
      if (!isValidType) {
        this.$Message.error(' Excel/CSV ')
        return false
      }
      // 10MB
      const isLt10M = file.size / 1024 / 1024 < 10
      if (!isLt10M) {
        this.$Message.error(' 10MB')
        return false
      }
      // 
      this.readFile(file)
      return false
    },
    // 
    readFile(file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result)
          const workbook = XLSX.read(data, { type: 'array' })
        
          // 
          const firstSheet = workbook.Sheets[workbook.SheetNames[0]]
          
          // JSON
          const jsonData = XLSX.utils.sheet_to_json(firstSheet)

          // 
          if (jsonData.length === 0) {
            this.$Message.error('')
            return
          }
          // 
          const columnMapping = {
            '': 'name',
            'IP': 'manage_ip',
            '': 'sn',
            'MAC': 'mac_address',
            '': 'vendor',
            '': 'model',
            '': 'idc',
            '': 'rack',
            'U': 'position',
            '': 'role',
            '':'status',
            '': 'description'
          }

          // 
          this.previewData = jsonData.map(row => {
            const newRow = {}
            Object.keys(row).forEach(key => {
              const newKey = columnMapping[key] || key
              if (row[key] === undefined || row[key] === null) {
                newRow[newKey] = ''
              } else {
              newRow[newKey] = row[key]
            }
          })
            return newRow
          })

        } catch (error) {
          this.$Message.error('' + error.message)
        }
      }
      reader.onerror = () => {
        this.$Message.error('')
      }
      reader.readAsArrayBuffer(file)
    },
    // 
    downloadTemplate(e) {
      e.stopPropagation()
      // 
      const wb = XLSX.utils.book_new()
      
      // 
      const wsData = [
        ['', 'IP', '', 'MAC', '', '', '', '', 'U', '', '', ''],
        ['test', '127.0.0.1', 'xxxxxx', 'xxxxxxx', '', 'xxxxx', 'xxxxx', 'xxxxx', '1', 'xxxxx', 'xxxxx', 'xxxxx']
      ]
      
      // 
      const ws = XLSX.utils.aoa_to_sheet(wsData)
      
      // 
      XLSX.utils.book_append_sheet(wb, ws, '')
      
      // 
      XLSX.writeFile(wb, 'switch_template.xlsx')
    },
    // 
    async handleImportSubmit() {
      if (!this.previewData.length) {
        this.$Message.warning('')
        return
      }
      try {
        this.importing = true
        const data = {
          data: this.previewData
        }
        let res = await importSwitch(data)
        if (res.code !== 0) {
           this.$Message.error(res.msg)
           this.importing = false
           return
        }
        this.importResult = res.data
        this.importModal.show = false
        this.resultModal.show = true

        if (res.data.failed_count === 0) {
          this.previewData = []
          this.handleGetData()
        } else {
          this.importing = false
          this.$Message.error(': ');
        }
      } catch (error) {
        this.$Message.error('' + error.message)
      } finally {
        this.importing = false
      }
    },
    // 
    handleResultClose() {
      this.resultModal.show = false
      if (this.importResult.failed_count === 0) {
        this.previewData = []
      }
    }
  },
  watch: {
    searchValue (val) {
      this.pageNum = 1
      this.handleGetData()
    },
  },
  computed: {
  },
  mounted () {
    this.handleGetData()
  }
}
</script>
